/* General body styling */
body {
    font-family: Arial, sans-serif;
    background-color: #e0f7fa;
    margin: 0;
    padding: 0;
    text-align: center;
  }
  
  /* Container for the entire app */
  .container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  /* Title styling */
  h1 {
    color: #007bff;
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  /* Input and button container styling */
  .input-container {
    margin: 20px 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  /* Input box styling */
  input[type="text"] {
    padding: 10px;
    width: 300px;
    font-size: 1rem;
    border: 2px solid #007bff;
    border-radius: 5px;
    margin-right: 10px;
    transition: border-color 0.3s;
  }
  
  input[type="text"]:focus {
    border-color: #0056b3;
    outline: none;
  }
  
  /* Button styling */
  button {
    padding: 10px 20px;
    font-size: 1rem;
    background-color: #007bff;
    color: rgb(238, 16, 16);
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
  }
  
  button:hover {
    background-color: #0056b3;
    transform: scale(1.05);
  }
  
  /* Error message styling */
  .error {
    color: red;
    font-weight: bold;
    margin-top: 10px;
  }
  
  /* Table styling */
  table {
    width: 80%;
    margin: 20px auto;
    border-collapse: collapse;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
  }
  
  th, td {
    border: 1px solid #ca1313;
    padding: 10px;
    text-align: left;
  }
  
  th {
    background-color: #007bff;
    color: rgb(228, 27, 27);
    font-weight: bold;
    text-transform: uppercase;
  }
  
  td {
    font-size: 1rem;
  }
  
  tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  tr:hover {
    background-color: #eaf4ff;
  }
  
  /* Responsive Design */
  @media screen and (max-width: 600px) {
    input[type="text"] {
      width: 80%;
    }
  
    button {
      width: 80%;
      margin-top: 10px;
    }
  
    table {
      width: 100%;
    }
  }
  